@import url(https://fonts.googleapis.com/css?family=Playfair+Display:500,700,900|Quicksand:400,500&display=swap);
.scroll {
  opacity: 0.8;
  background-color: #fe8c00;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border-radius: 10px;
  border: none;
  outline: none;
  z-index: 8;
  cursor: pointer;
}
.scroll:focus {
  outline: none;
}
.scroll:hover {
  opacity: 1;
}

.arrow-up {
  color: white;
}

.testimonial-container {
  margin: 0 auto;
  padding: 10px;
}

.testimonial {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 0 auto;
  min-width: 230px;

  width: 100%;
  color: #000000;
  text-align: center;
  font-size: 16px;
  background: #2193b0; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(248, 181, 0, 0.2)),
    to(rgba(33, 147, 176, 0.7))
  );
  background: linear-gradient(
    to bottom,
    rgba(248, 181, 0, 0.2),
    rgba(33, 147, 176, 0.7)
  );
  padding: 15px;
  box-sizing: border-box;
}

.testimonial *,
.testimonial *:before,
.testimonial *:after {
  box-sizing: border-box;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.testimonial figcaption {
  position: relative;
  width: 100%;
}

.testimonial h5,
.testimonial h4,
.testimonial blockquote {
  margin: 0;
}

.testimonial h5,
.testimonial h4 {
  font-weight: 300;
}

.testimonial h4 {
  color: #ffffff;
}

.testimonial h5 {
  color: #a6a6a6;
}

.testimonial blockquote {
  font-size: 1em;
  padding: 45px 20px 40px 50px;
  margin-top: 30px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: inset -1.4px -1.4px 2px rgba(0, 0, 0, 0.3);
  text-align: left;
  position: relative;
}

.testimonial blockquote:before {
  font-family: "FontAwesome";
  content: "\201C";
  position: absolute;
  font-size: 80px;
  opacity: 0.25;
  font-style: normal;
  top: 30px;
  left: 20px;
}

.testimonial .profile {
  width: 100px;
  border-radius: 50%;
  display: inline-block;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.4);
  margin-bottom: 10px;
  border: solid 5px #fe8c00;
}
